<template>
    <div class="outBoxacc">
        <van-nav-bar
        title="账号管理"
        left-text="返回"
        left-arrow
        @click-left="$router.go(-1)"
        fixed
        />
        
        <!--  -->
        
        <van-cell-group>
            <van-cell title="头像" center>
                <template #right-icon>
                    <div class="headPhoto">
                        <img :src="accountMess.userImg" alt="">
                        <van-uploader :after-read="afterRead" />
                    </div>
                </template>
            </van-cell>
            <van-cell title="用户ID" :value="accountMess.userId" />
            <van-cell title="手机号" :value="accountMess.phone" />
            
            <van-cell class="v-cell" title="昵称">
                <div class="clearfix">
                <div class="fr">
                    <van-field  @blur="changeUserResume({key: 'nickName', value: accountMess.nickName, url: '/updateNickName'})" class="v-filed" v-model="accountMess.nickName" input-align="right" />
                </div>
                </div>
            </van-cell>

            <van-cell>
                <div class="clearfix">
                <div class="fl v-cell-text">简介</div>
                <div class="fl v-cell-textarea">
                    <textarea  @change="changeUserResume({key: 'desc', value: accountMess.desc, url: '/updateDesc'})" class="textarea" v-model="accountMess.desc"></textarea>
                </div>
                </div>
            </van-cell>

        </van-cell-group>
    </div>
</template>

<script>
    import '../../assets/less/accountManagement.less'
    export default {
        data(){
            return {
                url:'',//头像图片链接
                accountMess:{},//账号信息
            }
        },
        created(){
            // this.url =  this.$store.state.userImg

            //账号管理
            this.getAccount()
        },
        methods:{
            
            
            //
            afterRead(file){
                
                let serverBase64Img = file.content.split(',')[1]
                //上传背景
                this.uploaderHeadPhoto(serverBase64Img)
            },
            //上传头像
            uploaderHeadPhoto(serverBase64Img){

                let tokenString = localStorage.getItem("__Tk");
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                    //规定图片类型
                let imgType = ['jpg','jpeg','png']

                this.axios({
                    method: "POST",
                    url: "/updateAvatar",
                    data: {
                    appkey: this.appkey,
                    tokenString,
                    ...imgType,
                    serverBase64Img
                    }
                })
                    .then(result => {
                    this.$toast.clear();
                    
                    if (result.data.code == 'H001') {
                        //更换背景图片
                        this.accountMess.userImg = result.data.userImg;
                    }
                    })
                    .catch(() => {
                    this.$toast.clear();
                    
                    });
            },

            //账号管理
            getAccount(){
                let tokenString = localStorage.getItem("__Tk");
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "GET",
                    url: "/findAccountInfo",
                    params: {
                    appkey: this.appkey,
                    tokenString,
                    }
                })
                    .then(result => {
                    this.$toast.clear();
                    
                    if (result.data.code == 'B001') {
                        this.accountMess = result.data.result[0]
                        
                        
                    }
                    })
                    .catch(() => {
                    this.$toast.clear();
                    
                    });
            },

            //修改昵称、修改简介
            changeUserResume(obj){
                let tokenString = localStorage.getItem("__Tk");
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "POST",
                    url: obj.url,
                    data: {
                    appkey: this.appkey,
                    tokenString,
                    [obj.key]: obj.value
                    }
                })
                    .then(() => {
                    this.$toast.clear();
                    
                    // if (result.data.code == 'B001') {
                    //     this.accountMess = result.data.result[0]
                    //     
                        
                    // }
                    })
                    .catch(() => {
                    this.$toast.clear();
                    
                    });
            }

        },

        //离开组件前，保存userImg  的url到vuex中
        // beforeDestroy(){
        //     this.$store.state.userImg = this.url
        // }
    }
</script>

<style lang="less" scoped>

</style>